import { Vuepress } from '@vuepress/client/lib/components/Vuepress'

const routeItems = [
  ["v-8daa1a0e","/","Tutoriels",["/index.html","/README.md"]],
  ["v-2186efe5","/cms/odoo.html","Odoo 12",["/cms/odoo","/cms/odoo.md"]],
  ["v-7445767f","/cms/","CMS",["/cms/index.html","/cms/README.md"]],
  ["v-76677b6e","/electronique/gpio.html","GPIO",["/electronique/gpio","/electronique/gpio.md"]],
  ["v-1e6463ae","/tools/setup.html","Installation d'un PC",["/tools/setup","/tools/setup.md"]],
  ["v-5c1cd6c9","/dev/laravel/","Laravel",["/dev/laravel/index.html","/dev/laravel/README.md"]],
  ["v-8b8e5cfc","/dev/vue_js/vuepress.html","Vuepress",["/dev/vue_js/vuepress","/dev/vue_js/vuepress.md"]],
  ["v-b117ad6c","/dev/laravel/livewire/installation.html","",["/dev/laravel/livewire/installation","/dev/laravel/livewire/installation.md"]],
  ["v-3706649a","/404.html","",["/404"]],
]

export const pagesRoutes = routeItems.reduce(
  (result, [name, path, title, redirects]) => {
    result.push(
      {
        name,
        path,
        component: Vuepress,
        meta: { title },
      },
      ...redirects.map((item) => ({
        path: item,
        redirect: path,
      }))
    )
    return result
  },
  [
    {
      name: "404",
      path: "/:catchAll(.*)",
      component: Vuepress,
    }
  ]
)
